import React from 'react';
import { Button, Jumbotron} from 'reactstrap';

//navbar-color: '#343a40'
const jumbotronStyle = {
    'backgroundColor': '#3e444c',
    'borderRadius': '0',
    'color': '#FFC224',
    'alignContent': 'center',
    'alignSelf': 'center',
    'textAlign': 'center',
    'height': '93vh',
}

const buttonStyle = {
    'fontSize': '2vh',
    'padding': '1.2vh 1.1vw 1.2vh 1.1vw',
    'marginTop': '2vh',
}

const margin = {
    'marginTop': '2vh',
}

const CoolDescription = (props) => {
  return (
    <div>
        <Jumbotron style={jumbotronStyle}>
            <h1 className="display-1">CoolSchool</h1>
            <p className="lead">Your school wouldn't teach you, what we will.</p>
            <hr style={{'borderColor' : '#666'}}className="my-2" />
            <p style={margin}>So if you want to learn something important, what you will propably find useful in your life, fell free to join us by clicking button below.</p>
            <p className="lead">
            <Button style={buttonStyle} href="/register" color="warning">Start Learning Now</Button>
            </p>
        </Jumbotron>
    </div>
  );
};

export default CoolDescription;