import React from 'react';
import { 
  Button,
  Modal, 
  ModalHeader, 
  ModalBody,
  Form,
  FormGroup,
  Label,
  Input } from 'reactstrap';

class LogInModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState(prevState => ({
      modal: !prevState.modal
    }));
  }

  render() {
    return (
      <div>
        <Button color="warning" onClick={this.toggle}>Log In</Button>
        <Modal isOpen={this.state.modal} toggle={this.toggle}>
          <ModalHeader toggle={this.toggle}>Welcome back!</ModalHeader>
          <ModalBody>
          <Form action="/login" method="post" id="log-in-form" name="userDetails">
            <FormGroup>
              <Label for="userEmail">Email</Label>
              <Input type="email" name="email" id="userEmail" placeholder="name@example.com" />
            </FormGroup>
            <FormGroup>
              <Label for="userPassword">Password</Label>
              <Input type="password" name="password" id="userPassword" placeholder="********" />
            </FormGroup>
            <Button type="submit" color="warning" onClick={this.toggle}>Log In</Button>
          </Form>
          </ModalBody>
        </Modal>
      </div>
    );
  }
}

export default LogInModal;