import React from 'react';
import {
  Button,
  Navbar,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink } from 'reactstrap';
import LogInModal from './LogInModal'

const navbarStyle = {
  'height': '7vh',
}

class Navigation extends React.Component {
  render() {
    return (
      <div >
        <Navbar style={navbarStyle} color="dark" dark expand="md" id="navbar">
          <NavbarBrand className="text-warning" href="/"><h3>CoolSchool</h3></NavbarBrand>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink>
                  <LogInModal />
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="https://github.com/grupadotnet/coolschool"><Button color="warning">GitHub</Button></NavLink>
              </NavItem>
            </Nav>
        </Navbar>
      </div>
    );
  }
}

export default Navigation;